body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.title {
  margin: 0;
  padding: 0;
}
.calculator {
  text-align: center;
  background-color: #58427c;
  width: 65vw;
  display: block;
  border-radius: 20px;
  margin: auto;
  box-shadow: 5px 10px; 
}
.invisible {
  height: 0vh;
  color: #58427c;
}
body, html {
  background-color: darkgrey;
}

.result {
  height: 15vh;
  width: 57.5vw;
  margin-left: 2.5vw;
  background-color: black;
  color: white;
  font-size: 4rem;
  text-align: right;
  padding-right: 2.5vw;
}

.input {
  height: 15vh;
  width: 57.5vw;
  background-color: black;
  color: white;
  font-size: 4rem;
  text-align: right;
  padding-right: 2vw;
  outline: none;
  border: none; 
}

.clear {
  background-color: #505050;
  width: 15vw;
  height: 15vh;
  border: 0.05rem solid black;
  font-size: 3rem;
  outline: none;
  color: white;
  transition: 1s;
}
.key {
  background-color: grey;
  width: 15vw;
  height: 15vh;
  border: 0.05rem solid black;
  font-size: 3rem;
  outline: none;
  transition: 1s;
}
.att {
  background-color: rgba(88,66,124,0.2);
  width: 15vw;
  height: 15vh;
  border: 0.05rem solid black;
  font-size: 3rem;
  outline: none;
  transition: 1s;
  color: white;
}
.num {
  background-color: #303030;
  color: #eee;
  width: 15vw;
  height: 15vh;
  border: 0.05rem solid black;
  font-size: 3rem;
  outline: none;
  transition: 1s;
}
.num:hover {
  background-color: #58427c;
}
.att:hover {
  background-color: #303030;
}
.clear:hover {
  background-color: #eee; 
}
.key:hover {
  background-color: white;
}
.button {
  padding-bottom: 5vh;
}

@-webkit-keyframes flicker {
  from {
    background-color: rgba(0,0,0,1);
  } to {
    background-color: rgba(255,255,255,1);
  }
}

@keyframes flicker {
  from {
    background-color: rgba(0,0,0,1);
  } to {
    background-color: rgba(255,255,255,1);
  }
}

.pulse:active {
  -webkit-animation: flicker 1s 1 ease-out;
          animation: flicker 1s 1 ease-out;
}

